.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: black;
}
.nav-padding {
  font-size: 20px;
  width: 100%;
  background-color: white;
  padding: 3%;
  display: flex;
  padding-bottom: 0;
  height: 70px;
  align-items: center;
  position: absolute;
  top: 0px;
}

.nav-link {
  padding: 15px;
}
h2 {
  color: #000000;
  padding: 3px;
}

.App-navigation {
  font-size: 12px;
  padding: 0%;
  display: flex;
  justify-content: space-between;
  padding-bottom: 2px;
  height: auto;
  align-items: center;
  position: absolute;
  top: 0px;
}